<template>
  <div class="radio">
    <input type="radio" :id="cid" :name="cname" :value="cvalue">
    <label :for="cid" class="radio-label"><slot name="label"></slot></label>
  </div>
</template>

<script>
export default {
  name: 'Radio',
  props: {
    cid: {
      type: String,
      default() {
        return ''
      }
    },
    cname: {
      type: Number,
      default() {
        return ''
      }
    },
    cvalue: {
      type: String,
      default() {
        return ''
      }
    }
  }
}
</script>

<style scoped>
  .radio {
    margin: 0.8rem;
  }
  .radio input[type="radio"] {
    position: absolute;
    opacity: 0;
  }
  .radio input[type="radio"] + .radio-label:before {
    content: '';
    background: #f4f4f4;
    border-radius: 100%;
    border: 1px solid #b4b4b4;
    display: inline-block;
    width: 1em;
    height: 1em;
    position: relative;
    margin-right: 1em;
    vertical-align: bottom;
    cursor: pointer;
    text-align: center;
    -webkit-transition: all 250ms ease;
    transition: all 250ms ease;
  }
  .radio input[type="radio"]:checked + .radio-label:before {
    background-color: #3197EE;
    box-shadow: inset 0 0 0 4px #f4f4f4;
  }
  .radio input[type="radio"]:focus + .radio-label:before {
    outline: none;
    border-color: #3197EE;
  }
  .radio input[type="radio"]:disabled + .radio-label:before {
    box-shadow: inset 0 0 0 4px #f4f4f4;
    border-color: #b4b4b4;
    background: #b4b4b4;
  }
  .radio input[type="radio"] + .radio-label:empty:before {
    margin-right: 0;
  }
</style>